import React from 'react';
import { Link } from 'react-router-dom';

function Signup(props) {
    return (
        <div className='login_bg'>
            <div className='bg-white/5 w-full h-full flex justify-center pt-32'>
                <div>
                    <img src='/img/logo.png' className='w-16 h-20 m-auto' alt='' />

                    <div className='bg-white rounded-3xl p-10 mt-10 w-[500px]'>
                        <p className='text-2xl font-medium'>Create a free account</p>
                        <div className='mt-4'>
                            <p className='font-medium text-gray-400'>Your best email</p>
                            <input type={"text"} placeholder="Your email" className='w-full p-2 outline-0 border border-gray-200' />
                        </div>
                        <div className='mt-4'>
                            <p className='font-medium text-gray-400'>Your first name</p>
                            <input type={"text"} placeholder="Used to create your referral coupon" className='w-full p-2 outline-0 border border-gray-200' />
                        </div>
                        <div className='mt-4'>
                            <p className='font-medium text-gray-400'>Country</p>
                            <input type={"text"} placeholder="France" className='w-full p-2 outline-0 border border-gray-200' />
                        </div>
                        <div className='mt-4'>
                            <p className='font-medium text-gray-400'>Password (must be at least 6 characters)</p>
                            <input type={"password"} placeholder="****" className='w-full p-2 outline-0 border border-gray-200' />
                        </div>
                        <div className='mt-5'>
                            <Link to="/"><button className='w-full py-2 bg-[#DC9217] text-white'>Create my account</button></Link>
                            <Link to="/login"><p className='text-[#DC9217] text-center mt-5 cursor-pointer'>Already have an account?</p></Link>
                        </div>


                    </div>

                </div>

            </div>
            
        </div>
    );
}

export default Signup;