import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import ListToken from './../../components/section/ListToken';

const Navbar = () => {

    const [isShow, toggleModal] = useState(false);

    const [isListModalShow, setListModalShow] = useState(false);
    
    return (
        <>
            <div className="bg-white py-3 z-20" >
                <div className="n-container">
                    <div className="flex justify-between items-center">
                        <Link to={"/"}>
                            <div className="flex items-center gap-2">
                                <img src="/logo.png" alt="logo" className="h-12 sm:h-16" />
                            </div>
                        </Link>

                        <div className="hidden sm:flex items-center text-black gap-5">
                            <p className='cursor-pointer' data-modal-toggle="defaultModal" onClick={() => setListModalShow(true)}>List token</p>
                            <Link to="/crypre"><p>Crypto Presentation</p></Link>
                            <Link to="/search_crypto"><p>Search Crypto</p></Link>
                            <Link to="/token"><p>Token Detail</p></Link>
                            <Link to="/search_crypto" className='bg-yellow-600 py-2 px-5 rounded' style={{background: "linear-gradient(90deg, #D47A09 0%, #EBBF33 100%)"}}><p>Access App</p></Link>
                            <Link to="/login" className='border border-[#D6800C] bg-white text-[#D6800C] rounded py-2 px-5'><p>Login</p></Link>
                        </div>

                        <p className="none sm:hidden text-black pointer" onClick={() => toggleModal(true)} >&#9776;</p>
                    </div>
                </div>
                    
                <div className={` ${ isShow ? "" : "hidden" } bg-white absolute left-0 top-0 h-screen w-full`}>
                    <p className="none sm:hidden text-black text-right pointer m-5 mt-6" onClick={() => toggleModal(false)} >&#9776;</p>

                    <div className='text-black text-lg text-center mt-12'>
                        <Link className='mt-5' to="/stakingReward" ><p>Pricing</p></Link>
                        <Link className='mt-5' to="/cryptoassets" ><p>Wallet connect</p></Link>
                        <Link className='mt-5' to="/providers" ><p>Access App</p></Link>
                        <Link className='mt-5' to="/calculator" ><p>Login</p></Link>
                    </div>
                </div>
            </div>

            <ListToken isShow={isListModalShow} setShow={setListModalShow} />
        </>
    )
}

export default Navbar;